.is-highlighted {
  box-shadow: 0 1px 25px #b5b5b5;
}

.is-highlighted.is-white {
  box-shadow: 0 1px 25px #e6e6e6;
}

.is-highlighted.is-black {
  box-shadow: 0 1px 25px black;
}

.is-highlighted.is-light {
  box-shadow: 0 1px 25px #dbdbdb;
}

.is-highlighted.is-dark {
  box-shadow: 0 1px 25px #1c1c1c;
}

.is-highlighted.is-primary {
  box-shadow: 0 1px 25px #009e86;
}

.is-highlighted.is-link {
  box-shadow: 0 1px 25px #205bbc;
}

.is-highlighted.is-info {
  box-shadow: 0 1px 25px #0f81cc;
}

.is-highlighted.is-success {
  box-shadow: 0 1px 25px #1ca64c;
}

.is-highlighted.is-warning {
  box-shadow: 0 1px 25px #ffd324;
}

.is-highlighted.is-danger {
  box-shadow: 0 1px 25px #ff0537;
}
